* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Gridcontainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)) , url(../../assets//manan_team.jpg);
  background-repeat: none;
  background-position: center;
  background-size: cover;
  height: 100%;
  color:  #CAD5E2;
  font-size: 1.2rem;
}

.Griditem {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}

p {
  margin-top: 10px;
}
